<template>
  <div class="container">
    <h1>Son İletiler</h1>
    <h2>Hazırlayan: Emre Ertunç</h2>
    <div align="left">
    <p><strong>KAYIT EKLEME:</strong> Kayıt Ekle kutucuğuna istenilen iletiyi yazıp 'EKLE' butonuna basın.</p>
    <p><strong>KAYIT DEĞİŞİKLİĞİ:</strong>Değişiklik yapmak için kayıtların altındaki sahada değiştirdikten sonra 'ENTER' tuşunu veya 'KAYDET' butonunu kullanın.</p>
    <p><strong>KAYIT SİLME:</strong> Silinmesi istenilen kaydın olduğu yeşil sahada herhangi bir yere çift tıklayınız.</p>
    
    </div>
    <div class="create-post">
      <label for="create-post">Kayıt Ekle: </label>
      <input type="text" id="create-post" v-model="text" placeholder="İletinizi yazın...">
      <button v-on:click="Kaydet">Ekle</button>
    </div>
    <hr>
    <p class="error" v-if="error">{{ error }}</p>
    <div>
      <div class="post" v-for="ileti in iletiler" v-bind:key="ileti._id"  v-on:dblclick="Sil(ileti._id)">

        <span>Oluşturma: {{ ileti.createdAt.getDate() }}.{{ ileti.createdAt.getMonth()+1 }}.{{ ileti.createdAt.getFullYear() }} </span>
        <br><span v-if="ileti.updatedAt!==''">Son Değişiklik:  {{ileti.updatedAt.substring(8,10)}}.{{ileti.updatedAt.substring(5,7)}}.{{ileti.updatedAt.slice(0,4)}}</span>

        <p class="text">{{ ileti.text }}</p>
        <label>İletiyi Güncelleme: </label>
        <input type="text" v-model="ileti.text" placeholder="update post" v-on:keyup.enter="Update(ileti._id, ileti.text)">
        <button v-on:click="Update(ileti._id, ileti.text)">Kaydet</button>

      </div>
    </div>
    
  </div>
</template>

<script>
//v-bind:item="ileti" v-bind:index="index"
import iletiService from '../iletiService';

export default {
  name: 'IletiComponent',
  data() {
    return {
      iletiler: [],
      error: '',
      text: '',
    }
  },

async created(){
  try {
    this.iletiler = await iletiService.getIletiler();
    }
    catch(err){
      this.error = err.message;
    }
  },
  methods: {


    async Kaydet(){
      await iletiService.insertIleti(this.text);
      this.iletiler = await iletiService.getIletiler();
    },

    async Sil(id) {
      await iletiService.deleteIleti(id);
      this.iletiler = await iletiService.getIletiler();
    },
  
    async Update(id, textt) {
      await iletiService.updateIleti(id, textt);
      this.iletiler = await iletiService.getIletiler();
    }
}
};
</script>

