import axios from 'axios';
//const axios = require('axios');

//const url = 'http://localhost:5000/api/iletiler/';
//const url = 'http://emre-ertunc-iletitakip.herokuapp.com/api/iletiler/';
const url = 'api/iletiler/';

class iletiService {
    //iletileri al (GET Posts)
    static getIletiler(){
        return new Promise(async(resolve,reject) => {
            try {
                const res = await axios.get(url);
                const data = res.data;
                resolve (
                    //axiostan okunan res dizisinin her elemani icin bir ileti ve tarih donduruyo
                    data.map(ileti => ({
                        ...ileti,
                        createdAt: new Date(ileti.createdAt)
                    }))
                );
            } catch (err) {
                reject (err);
            }
        });
    }

    //ileti ekle (Create Post)
    static insertIleti(text){
        return axios.post(url, {
            text: text
        });
    }

    //ileti sil (Delete Post)
    static deleteIleti(id) {
        return axios.delete(`${url}${id}`);
    }

    //ileti update deneme
    static updateIleti(id, text){
/*         document.write(id);
        document.write(text); */
        return axios.put(`${url}${id}`, {
            text: text
        });
    }
}
export default iletiService;